  text-shadow: 2px 2px 0 #f5f5f5;
  content: "\f0c8"; }

.dragDropIncorrectItemIcon:after {
  font-family: FontAwesome;
  font-size: 14px;
  position: absolute;
  top: -10px;
  left: -5px;
  color: #fff;
  content: "\f00d"; }

.dragDropCorrectItem {
